import React from 'react';

import '../cssfiles/Contact.css';
import Footer from './Footer';

function Contact() {
    return (
        <div>
            <div className="contact__head">Contact Us</div>
            <div className="contact__get">
                <p className="contact__get__head">Get in touch with <span style={{color:'#6415FF', fontSize:'40px'}}>Compters</span></p>
                <div className="contact__get__container">
                    <div className="contact__get__container__contact">
                        <p className="contact__get__container__contact__lable">Office: </p>
                        <p className="contact__get__container__contact__value">103/B - Shayamal Building, Nr. Mangadh Chowk, Varachha, Surat, India - 395006</p>
                        <div style={{height:'10px'}} />
                        <p className="contact__get__container__contact__lable">Email: </p>
                        <p className="contact__get__container__contact__value">office@comptersbookkeeping.com</p>
                        <div style={{height:'10px'}} />
                        <p className="contact__get__container__contact__lable">Phone No.: </p>
                        <p className="contact__get__container__contact__value">US:  <span className="contact__get__container__contact__valuespan">+1 8484848484</span></p>
                        <p className="contact__get__container__contact__value">Inida:  <span className="contact__get__container__contact__valuespan">+91 8460823668</span></p>
                    </div>
                    <div className="contact__get__container__form">
                        <p className="contact__get__container__form__lable">First Name (Required)</p>
                        <input className="contact__get__container__form__input" type='text' />
                        <p className="contact__get__container__form__lable">Last Name (Required)</p>
                        <input className="contact__get__container__form__input" type='text' />
                        <p className="contact__get__container__form__lable">Email (Required)</p>
                        <input className="contact__get__container__form__input" type='text' />
                        <p className="contact__get__container__form__lable">Phone (Required)</p>
                        <input className="contact__get__container__form__input" type='text' />
                        <p className="contact__get__container__form__lable">Message (Required)</p>
                        <input className="contact__get__container__form__input" type='text' />
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    )
}

export default Contact;
